.comment {
  display: none;
  position: absolute;
  z-index: 10000;
  color: #333;
  max-width: 30vw;
  background: rgba(255, 255, 255, 0.75);
  border-radius: 10px;
  transform: scale(0);
  transition: all 1s ease;
}

.comment.vis {
  display: block;
  transform: scale(1);
  transition: all 1s ease;
}
